					<scroll-view class="drawer-content-list" scroll-y="true">
						<view v-for="(item,index) in bluetooth" :key="item.deviceId" @click="createBLEConnection(item.deviceId,index)" class="sb-list">
							<view>
								{{item.name}}
							</view>
							<view class="m-lt-auto flex-nowrap align-items-center height-100">
								<view v-if="isLink[index]==0" class="ft-color-red ft-28 ft-color-999999">未连接</view>
								<view v-if="isLink[index]==1" class="ft-color-red ft-28 ft-color-999999">连接中...</view>
								<view v-if="isLink[index]==2" class="ft-color-red ft-28 ft-color-green ft-color-999999">已连接</view>
								<view v-if="isLink[index]==3" class="ft-color-red ft-28 ft-color-green ft-color-999999">连接失败</view>
								<view v-if="isLink[index]==4" class="ft-color-red ft-28 ft-color-green ft-color-999999">已断开</view>
								<view @click.stop="closeBLEConnection(item.deviceId,index)" class="ft-color-green iconfont icon-info m-lt20 ft-40"></view>
							</view>
						</view>
					</scroll-view>
				</view>
			</view>
		</view>
	<!-- 自定义导航栏 -->
			<view class="navBarBox">
				<!-- 状态栏占位 -->
				<view class="statusBar" :style="{ paddingTop: statusBarHeight + 'px' }"></view>
				<!-- 真正的导航栏内容 -->
				<view class="navBar">
					<image  src="/static/logo.png" mode="scaleToFill" @click.stop="drawerOpen" class="logo"></image>
					<view>天沃称量App</view>
					<view></view>
				</view>
			</view>
		<view class="data-box">
			<!-- <view class="img-box flex-wrap justify-content-center">
				<image src="/static/images/lianjie.png" mode=""></image>
			</view>
			<view class="ft-48 ft-weight-900 text-align-center m-t-52">
				PAIRING
			</view>
			<view class="ft-32 width-100 text-align-center">
				<view class="width-100">Please long press on theGREENHILL Robot</view> 
				<view class="width-100">button to turn it on. Then pressonce again</view> 
				<view class="width-100 m-t-48">the button to pair yourRobot</view>
			</view>
			<view class="width-100 text-align-center" style=""></view>
			<view class="width-100 text-align-center m-t-52">
				<view class="iconfont icon-xiala2-01 ft-color-grey"></view>
			</view>
			<view class="width-100 text-align-center ft-36 ft-weight-700 m-t-52">
				MANUAL PAIRING
			</view>
			<view class="pd-f-r-40 width-100 flex-nowrap align-items-center m-t-24">
				<view>SEARCHING PIQ ROBOT</view>
				<view class="m-lt-auto iconfont icon-jiazai ft-color-grey"></view>
			</view>
			<view class="ft-32 pd-f-r-40 m-t-24">
				Ensure yourGREENHILL is turned ON and
				thatyour phone Bluetooth is ON. No need
				to enterany code for pairing, just select
				your Robot inthe list below.
			</view>
			<view></view>
			<view style="height: 84upx;line-height: 60upx;" class="pd-f-r-40 border-bt12 ft-28 ft-color-grey">
				Discovery equipment
			</view>
			<view class="pd-f-r-36">
				<view class="">
					蓝牙设备服务uuid
				</view>
				<view v-for="item in serverList" :key="item.uuid" class="">
					<view class="ft-color-red">
						 uuid : {{item.uuid}}
					</view>
					<view class="ft-color-green">
						 isPrimary : {{item.isPrimary}}
					</view>
				</view>
				<view style="color: #528CE0;">当前使用的服务uuid:{{serviceId}}</view>
				<view class="">
					蓝牙设备特征uuid和设备拥有的权限 true 为有 false 为无
				</view>
				<view style="color: #528CE0;">当前使用的特征uuid:{{characteristicId}}</view>
				<view v-for="item in characteristics" :key="item.uuid" class="">
					<view class="ft-color-red">
						 uuid : {{item.uuid}}
					</view>
					<view class="ft-color-green">
						 read权限 : {{item.properties.read}}
					</view>
					<view class="ft-color-green">
						 write权限 : {{item.properties.write}}
					</view>
					<view class="ft-color-green">
						 notify权限 : {{item.properties.notify}}
					</view>
					<view class="ft-color-green">
						 indicate权限 : {{item.properties.indicate}}
					</view>
				</view> -->
				
			<!-- 	<view class="ft-color-red">
					读取设备返回的信息码: ({{readCode}})
				</view>
				<view class="ft-color-red">
					读取返回数据: ({{readCodeMsg}})
				</view> -->
				<!-- <button @click="onBLECharacteristicValueChange"> 启用NOTIFY </button> -->
				
				<!-- <view class="ft-color-red">
					读取NOTIFY返回mac地址: ({{macAddress}})
				</view> -->
				<view style="width: 100%;display: flex;flex-direction: column;align-items: center;">
					<view>
						{{new Date(lastTime)}}:
					</view>
					<view class="ft-color-red" style="margin:20rpx 0">
						<!-- 读取NOTIFY返回数据 -->
						 <div class="zhanshi">({{macValue}})</div>
					</view>
					<!-- <button @click="readBLECharacteristicValue" style="margin:20rpx 0"> 读取 </button> -->
				</view>
				<!-- <view class="">
					0 表示读取成功，
					10003 连接失败，
					10004 没有找到指定服务，
					10005 没有找到指定特征值，
				    10006 当前连接已断开，
					10007 当前设备特征值不支持读取操作，
					10009 Android 系统特有，系统版本低于 4.3 不支持 BLE
				</view> -->
				
				<view style="margin-top: 200px;">
					<input style="width: 100%;height: 100rpx;border: 1px solid #000000;" type="text" v-model="value" value="" />
					<button @click="writeBLECharacteristicValue">读取</button>
				</view>
				<!-- <view>发送回调数据：{{returnMessage}}</view> -->
			</view>
			
		</view>
		
	</view>
</template>
 
<script>

	export default {
		
		data() {
			return{
				isLogin: false,
				stateDrawer: false,
				startTime: null,  // 手势滑动时间
				startPosition: 0, // 手势进入时
				endPosition: 0 ,   // 手势离开时
				isShow:false,
				// 状态栏高度
				statusBarHeight: 0,
					// 导航栏高度
				navBarHeight: 82+11,
				bluetooth:[],
				isLink:[],
				// 调试数据
				serverList:[],
				characteristics:[],
				readCode:'',
				readCodeMsg:'',
				serviceId:'',
				characteristicId:'',
				value:'S',
				returnMessage:'',
				macAddress:"",
				macValue:'',
				lastTime:0,
			}
		},
		onLoad() {
			//在页面加载时候初始化蓝牙适配器
			uni.openBluetoothAdapter({
				success:(e) => {
					console.log('初始化蓝牙成功:' + e.errMsg);
					// 初始化完毕开始搜索
					this.startBluetoothDeviceDiscovery()
				},
			});
		},
	
			//第一次加载时调用
					created() {
						//获取手机状态栏高度
						this.statusBarHeight = uni.getSystemInfoSync()['statusBarHeight'];
					},
		
		methods: {
			drawerClose() {
				this._changeDrawer('stateDrawer', false)
			},
			drawerOpen() {
				this._changeDrawer('stateDrawer', true)
			},
			_changeDrawer(param, status) {
				this[param] = status
			},
			// 手势进入时
			touchStart(e) {
				this.startTime = Date.now()
				this.startPosition = e.changedTouches[0].clientX
			},
			// 手势离开时
			touchEnd(e){
				const endTime = Date.now()
				if (endTime - this.startTime > 2000){
					return;
				}
				this.endPosition = e.changedTouches[0].clientX;
				var slideDistance = this.endPosition - this.startPosition; 
				if( slideDistance > 100){
					this.drawerOpen()
				}
				if(slideDistance < -100){
					this.drawerClose()
				}
			},
			lanyaShow(){
				
			},
            startBluetoothDeviceDiscovery() {
                //在页面显示的时候判断是都已经初始化完成蓝牙适配器若成功，则开始查找设备
				uni.startBluetoothDevicesDiscovery({
					services: ['0000FFE0'],
					success: res => {
						console.log(res)
						this.onBluetoothDeviceFound();
					},
					fail: res => {
						uni.showToast({
							icon: "none",
							title: "查找设备失败！",
							duration: 3000
						})
					}
				});
            },
            /**
             * 停止搜索蓝牙设备
             */
            stopBluetoothDevicesDiscovery() {
                uni.stopBluetoothDevicesDiscovery({
                    success: e => {
                        console.log('停止搜索蓝牙设备:' + e.errMsg);
                    },
                    fail: e => {
                        console.log('停止搜索蓝牙设备失败，错误码：' + e.errCode);
                    }
                });
            },
            /**
             * 发现外围设备
             */
            onBluetoothDeviceFound() {
                console.log("监听寻找新设备");
                // this.getBluetoothDevices();
                uni.onBluetoothDeviceFound(devices => {
                    console.log('开始监听寻找到新设备的事件');
					console.log(devices)
                    this.getBluetoothDevices();
                });
            },
            /**
             * 获取在蓝牙模块生效期间所有已发现的蓝牙设备。包括已经和本机处于连接状态的设备。
             */
            getBluetoothDevices() {
                console.log("获取蓝牙设备");
                uni.getBluetoothDevices({
                    success: res => {
                        console.log('获取蓝牙设备成功:' + res);
                        this.bluetooth = res.devices;
						this.bluetooth.forEach((item)=>{
							this.isLink.push(0)
						})
												
                    }
                });
            },
			//断开蓝牙连接
			closeBLEConnection(deviceId,index){
				uni.closeBLEConnection({
				  deviceId:deviceId,
				  success:res=> {
					  this.isLink.splice(index,1,4)
				    console.log(res)
				  }
				})
			},
			// 连接蓝牙设备
			createBLEConnection(deviceId,index){
				this.deviceId = deviceId;
				if(this.isLink[index] == 2){
					return;
				}
				this.isLink.splice(index,1,1)
				uni.createBLEConnection({
				  deviceId:this.deviceId,
				  success:res=> {
					console.log(res)
					this.isLink.splice(index,1,2)
					this.stopBluetoothDevicesDiscovery();
					this.getBLEDeviceServices(this.deviceId,index);
				  },
				  fail:res=> {
					this.isLink.splice(index,1,3)
					console.log(res)
				  }
				})	
			},
			//获取蓝牙设备所有服务(service)。
			getBLEDeviceServices(deviceId,index){
				setTimeout(()=>{
					uni.getBLEDeviceServices({
					  // 这里的 deviceId 需要已经通过 createBLEConnection 与对应设备建立链接
					  deviceId:deviceId,
					  success:(res)=>{
					    console.log('device services:', res)
						this.serverList = res.services
						res.services.forEach((item)=>{
							if(item.uuid.indexOf("FFE0")!=-1){
								this.serviceId = item.uuid;
								console.log('serverId:', this.serviceId)
								this.getBLEDeviceCharacteristics(this.deviceId)
							}
						})
					  }
					})
				},1000)
 
			},
			// 获取蓝牙特征值
			getBLEDeviceCharacteristics(deviceId){
				console.log("进入特征");
				setTimeout(()=>{
					uni.getBLEDeviceCharacteristics({
					  // 这里的 deviceId 需要已经通过 createBLEConnection 与对应设备建立链接
					  deviceId:deviceId,
					  // 这里的 serviceId 需要在 getBLEDeviceServices 接口中获取
					  serviceId:this.serviceId,
					  success:(res)=>{
					    console.log(res)
						this.characteristics = res.characteristics
						res.characteristics.forEach((item)=>{
							if(item.uuid.indexOf("FFE1")!=-1){
								this.characteristicId = item.uuid;
								console.log('characteristicId:', this.characteristicId)
								this.notifyBLECharacteristicValueChange(this.deviceId)
							}
						})
					  },
					  fail:(res)=>{
						console.log(res)
					  }
					})
				},2000)
			},
			// 启用 notify 功能
			notifyBLECharacteristicValueChange(deviceId){
				console.log(111)
				uni.notifyBLECharacteristicValueChange({
				  state: true, // 启用 notify 功能
				  // 这里的 deviceId 需要已经通过 createBLEConnection 与对应设备建立链接
				  deviceId:deviceId,
				  // 这里的 serviceId 需要在 getBLEDeviceServices 接口中获取
				  serviceId:this.serviceId,
				  // 这里的 characteristicId 需要在 getBLEDeviceCharacteristics 接口中获取
				  characteristicId:this.characteristicId,
				  success:(res)=> {
				    console.log('notifyBLECharacteristicValueChange success', res.errMsg)
					// this.onBLECharacteristicValueChange(this.deviceId);
				  },
				  fail:(res)=> {
				  	console.log('notifyBLECharacteristicValueChange success', res.errMsg)
				  }
				})
			},
			// ab2hex(buffer) {
			//   const hexArr = Array.prototype.map.call(
			//     new Uint8Array(buffer),
			//     function (bit) {
			//       return ('00' + bit.toString(16)).slice(-2)
			//     }
			//   )
			//   return hexArr.join('')
			// },
			arrayBufferToString(buffer) {
			    return String.fromCharCode.apply(null, new Uint8Array(buffer))
			},
			// 监听低功耗蓝牙设备的特征值变化
			onBLECharacteristicValueChange(deviceId){
				console.log(this.lastTime)				
				uni.onBLECharacteristicValueChange((res)=> {
					if (!this.lastTime || new Date().getTime() - this.lastTime > 1000) this.macValue = ''
					this.lastTime = new Date().getTime()
				
				  console.log(`characteristic ${res.characteristicId} has changed, now is ${res.value}`)
				  // console.log(this.ab2hex(res.value))
				  this.macAddress = res.deviceId;
				  this.macValue += this.arrayBufferToString(res.value);
				  console.log(this.macValue)
				  // this.macValue = res.value				  
				  // this.readBLECharacteristicValue(this.deviceId)
				  
				})	
			},
			// 读取设备二进制数据
			readBLECharacteristicValue(){
				// console.log('进入读取');
				// setTimeout(()=>{
					uni.readBLECharacteristicValue({
					  // 这里的 deviceId 需要已经通过 createBLEConnection 与对应设备建立链接
					  deviceId:this.deviceId,
					  // 这里的 serviceId 需要在 getBLEDeviceServices 接口中获取
					  serviceId:this.serviceId,
					  // 这里的 characteristicId 需要在 getBLEDeviceCharacteristics 接口中获取
					  characteristicId:this.characteristicId,
					  success:(res)=> {
					     console.log('readBLECharacteristicValue:', res)
						 this.readCode = res.errCode;
						 this.readCodeMsg = res.errMsg;
						 this.onBLECharacteristicValueChange(this.deviceId);
					  },
					  fail:(res)=> {
					  	 console.log('readBLECharacteristicValue:', res)
						 this.readCode = res.errCode;
						 this.readCodeMsg = res.errMsg;
						 this.onBLECharacteristicValueChange(this.deviceId);
					  }
					})
				// },200)
 
			},
 
			stringToArrayBuffer(str) {
			var bytes = new Array();
			var len, c;
			len = str.length;
			for (var i = 0; i < len; i++) {
				c = str.charCodeAt(i);
				if (c >= 0x010000 && c <= 0x10FFFF) {
					bytes.push(((c >> 18) & 0x07) | 0xF0);
					bytes.push(((c >> 12) & 0x3F) | 0x80);
					bytes.push(((c >> 6) & 0x3F) | 0x80);
					bytes.push((c & 0x3F) | 0x80);
				} else if (c >= 0x000800 && c <= 0x00FFFF) {
					bytes.push(((c >> 12) & 0x0F) | 0xE0);
					bytes.push(((c >> 6) & 0x3F) | 0x80);
					bytes.push((c & 0x3F) | 0x80);
				} else if (c >= 0x000080 && c <= 0x0007FF) {
					bytes.push(((c >> 6) & 0x1F) | 0xC0);
					bytes.push((c & 0x3F) | 0x80);
				} else {
					bytes.push(c & 0xFF);
				}
			}
			var array = new Int8Array(bytes.length);
			for (var i = 0; i <= bytes.length; i++) {
				array[i] = bytes[i];
			}
			return array.buffer;
			},
			// 发送二进制数据
			writeBLECharacteristicValue(){
				// let buffer = new ArrayBuffer(`${this.value}\r\n`)
				// // let buffer = new ArrayBuffer(this.value)
				// const dataView = new DataView(buffer)
				// dataView.setUint8(0, 0)
				let buffer = this.stringToArrayBuffer(`${this.value}\r\n`)
				uni.writeBLECharacteristicValue({
				  // 这里的 deviceId 需要在 getBluetoothDevices 或 onBluetoothDeviceFound 接口中获取
				  deviceId:this.deviceId,
				  // 这里的 serviceId 需要在 getBLEDeviceServices 接口中获取
				  serviceId:this.serviceId,
				  // 这里的 characteristicId 需要在 getBLEDeviceCharacteristics 接口中获取
				  characteristicId:this.characteristicId,
				  // 这里的value是ArrayBuffer类型
				  value: buffer,
				  success:(res)=> {
					this.returnMessage = res.errMsg;
					console.log('writeBLECharacteristicValue success', res)
				  },
				  fail:(res)=> {
					this.returnMessage = res.errMsg;
				  	console.log('writeBLECharacteristicValue success', res)
				  }
				})				
			}
		}
	}
</script>
 
